/* Website Design Color System - Navy Blue & Gray Theme */
:root {
  /* Primary Navy Blues */
  --color-primary-navy: #000776;
  --color-secondary-navy: #003366;
  --color-tertiary-navy: #0A1128;
  
  /* Gray Scale */
  --color-medium-gray: #8E8982;
  --color-cool-gray: #C0C0C0;
  --color-charcoal-gray: #36454F;
  
  /* Neutrals */
  --color-white: #FFFFFF;
  --color-light-gray: #F8F9FA;
  --color-black: #000000;
  
  /* Typography Scale - Design System */
  --font-family-primary: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-family-heading: 'Poppins', sans-serif;
  
  --font-size-xs: 0.75rem;    /* 12px */
  --font-size-sm: 0.875rem;   /* 14px */
  --font-size-base: 1rem;     /* 16px */
  --font-size-lg: 1.125rem;   /* 18px */
  --font-size-xl: 1.25rem;    /* 20px */
  --font-size-2xl: 1.5rem;    /* 24px */
  --font-size-3xl: 1.875rem;  /* 30px */
  --font-size-4xl: 2.25rem;   /* 36px */
  
  /* Font Weights */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  
  /* Accessibility - Contrast Ratios */
  --contrast-ratio-normal: 4.5;
  --contrast-ratio-large: 3.0;
  --contrast-ratio-ui: 3.0;
  
  /* Transitions */
  --transition-fast: 0.15s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.5s ease;
}

/* Material-UI CSS Variable Overrides */
:root {
  /* Primary Colors */
  --mui-palette-primary-main: var(--color-primary-navy);
  --mui-palette-primary-light: var(--color-secondary-navy);
  --mui-palette-primary-dark: var(--color-tertiary-navy);
  --mui-palette-primary-contrastText: var(--color-white);
  
  /* Secondary Colors */
  --mui-palette-secondary-main: var(--color-medium-gray);
  --mui-palette-secondary-light: var(--color-cool-gray);
  --mui-palette-secondary-dark: var(--color-charcoal-gray);
  --mui-palette-secondary-contrastText: var(--color-white);
  
  /* Background Colors */
  --mui-palette-background-default: var(--color-light-gray);
  --mui-palette-background-paper: var(--color-white);
  
  /* Text Colors */
  --mui-palette-text-primary: var(--color-tertiary-navy);
  --mui-palette-text-secondary: var(--color-charcoal-gray);
}

/* Global Base Styles */
* {
  box-sizing: border-box;
}

body {
  font-family: var(--font-family-primary);
  background-color: var(--color-light-gray);
  color: var(--color-tertiary-navy);
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

/* Layout Background Classes */
.main-content {
  background-color: var(--color-light-gray);
}

.content-section {
  background-color: var(--color-white);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.section-divider {
  border: none;
  height: 1px;
  background-color: var(--color-cool-gray);
  margin: 24px 0;
}

/* Footer Styles */
.footer {
  background-color: var(--color-charcoal-gray);
  color: var(--color-cool-gray);
  padding: 24px;
  margin-top: auto;
}

.footer a {
  color: var(--color-cool-gray);
  text-decoration: none;
  transition: var(--transition-normal);
}

.footer a:hover {
  color: var(--color-white);
}

/* Focus Indicators for Accessibility */
*:focus-visible {
  outline: 2px solid var(--color-secondary-navy);
  outline-offset: 2px;
}

/* Button Focus Override */
button:focus-visible,
.MuiButton-root:focus-visible {
  outline: 2px solid var(--color-secondary-navy);
  outline-offset: 2px;
}

/* Skip Link for Screen Readers */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: var(--color-primary-navy);
  color: var(--color-white);
  padding: 8px;
  text-decoration: none;
  border-radius: 4px;
  z-index: 9999;
  font-family: var(--font-family-primary);
  font-weight: 500;
}

.skip-link:focus {
  top: 6px;
}

/* Screen Reader Only Text */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
  :root {
    --color-primary-navy: #000000;
    --color-secondary-navy: #000000;
    --color-tertiary-navy: #000000;
    --color-charcoal-gray: #000000;
    --color-medium-gray: #666666;
    --color-cool-gray: #999999;
    --color-white: #FFFFFF;
    --color-light-gray: #FFFFFF;
  }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Color Blind Friendly Patterns */
.error-pattern {
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 2px,
    rgba(220, 38, 38, 0.1) 2px,
    rgba(220, 38, 38, 0.1) 4px
  );
}

.success-pattern {
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 2px,
    rgba(5, 150, 105, 0.1) 2px,
    rgba(5, 150, 105, 0.1) 4px
  );
}

.warning-pattern {
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 2px,
    rgba(217, 119, 6, 0.1) 2px,
    rgba(217, 119, 6, 0.1) 4px
  );
}